<template>
    <div class="login">
        <div v-if="warn">
            <p>私人博客，请勿登陆</p>
        </div>
        账号：<input v-model="loginId" placeholder="请输入账号"/>
        密码：<input v-model='loginPwd' placeholder="快输入密码！！！" type="password"/>
        <button @click="login">登陆</button>
    </div>
</template>


<script>
import api from '@/api'
export default {
    data(){
        return {
            loginId: '',
            loginPwd: '',
            warn: false
        }
    },
    methods: {
        login(){
            const data = {
                loginId : this.loginId,
                loginPwd: this.loginPwd
            }
            api.login(data).then(res=>{
                if(res.data.data && res.data.data.loginId === this.loginId){
                    this.$router.push({path: '/markdown'})
                }else{
                    this.warn = true;
                }

            })
        }
    },
}
</script>

<style lang="scss">
    @import '@/assets/css/login.scss';
</style>