<template>
  <div class="main-container">
    <div v-for="(value, index) in articleList" :key="index">
      <article-list :article="value" v-show="mShow ? mShow:couldShow === value.article_type"/>
    </div>
    <pager :total='total' @input="changePager"/>
  </div>
</template>

<script>
import api from '@/api'
import articleList from "./articleList.vue";
import pager from '../pager.vue'
export default {
  props:['couldShow'],
  created() {
    api.getArticleList().then((res) => {
      this.articleList = res.data.datas;
      this.total = res.data.total;
      this.$store.dispatch("articleList", res.data.datas);
    });
  },
  data() {
    return {
      mShow: this.$store.state.show,
      total: '',
      articleList: []
    };
  },
  watch: {
    couldShow: function(){
      this.mShow = this.$store.state.show
    } 
  },
  methods: {
    cons(){
      this.mShow = this.$store.state.show;
      console.log(this.mShow)
    },
    changePager(newPager){
      api.getArticleList(newPager).then((res) => {
      this.articleList = res.data.datas;
      this.total = res.data.total;
      this.$store.dispatch("articleList", res.data.datas);
    });
    }
  },
  
  components: {
    articleList,
    pager
  }
};
</script>

<style lang="scss">
@import "@/assets/css/main/index.scss";
</style>