import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    show: true,
    login : "success",
    articleList:[],
    content: ''
  },
  mutations: {
    showMe(state,val){
      state.show = val
    },
    addArticle(state,val){
      state.articleList = val;
    },
    addContent(state,val){
      state.content = val
    }
  },
  actions: {
    changeShow({commit}, val){
        commit('showMe', val);
    },
    only({commit}, val){
      commit('onlyShowMe',val);
    },
    articleList({commit}, val){
      commit('addArticle',val);
    },
    upContent({commit}, val){
      commit('addContent',val);
    }
  },
  modules: {
  }
})
