<template>
  <div class="wrap">
    <div
      class="box"
      @mouseenter="coming"
      @mouseleave="leave"
      v-for="(value, index) in typeArticle"
      @click="handleChange(value)"
      :key="index"
    >
      <div v-for="i in 6" :key="i">{{value}}</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      typeArticle: ["前端", "网络", "JS", "Python", "神经网络"],
      rot: [
        "rotateX(-180deg)",
        "rotateY(-180deg)",
        "rotateX(180deg)",
        "rotateY(180deg)",
      ],
      show: true,
    };
  },
  methods: {
    getDir(e, box) {
      var l = box.getBoundingClientRect().left;
      var t = box.getBoundingClientRect().top;
      var w = box.getBoundingClientRect().width;
      var h = box.getBoundingClientRect().height;
      var x = e.clientX - l - w / 2;
      var y = e.clientY - t - h / 2;
      //弧度转角度： 弧度/（Math.PI . 180)
      var deg = Math.atan2(y, x) / (Math.PI / 180);
      var d = (Math.round((deg + 180) / 90) + 3) % 4;
      return d;
    },
    coming(e) {
      var dom = e.target;
      var dir = this.getDir(e, dom);
      dom.style.transform = "translateZ(150px)" + this.rot[dir];
    },
    leave(e) {
      e.target.style.transform = "";
    },
    handleChange(value) {
      this.$emit('changeShow', value);
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/css/header/index.scss";
</style>