<template>
  <div>
    <div class="article-list">
      <div class="article-listitemline">
        <div class="article-listitemtitle">
          <a @click="readArticle(article.ArticleId)">
            <h1>{{title}}</h1>
          </a>
        </div>
        <div class="article-listitemaside">
          <span>{{article.createdAt.slice(0,10)}}</span>
        </div>
      </div>
      <p>{{article.article_intridyce}}</p>
      <router-link to="/">「{{article.article_type}}」</router-link>
      <a @click="readArticle(article.ArticleId)" class="article-listitemmore">阅读全文</a>
    </div>
  </div>
</template>


<script>
import api from '@/api'
export default {
  data(){
    return{
      show: this.$store.state.show,
      title: this.article.title,
      date: this.article.createdAt,
      intridyce: this.article.article_intridyce
    }
  },
  methods: {
    readArticle(id){
      api.getArticle(id).then(res=>{
          const data = res.data.content;
          const artHeader = {
            title:this.title,
            date: this.date,
            type: this.article.article_type,
            intridyce: this.intridyce
          }
          this.$store.dispatch('upContent', data);
          this.$router.push({path:`article/${id}`,query:artHeader});   
      })
    }
  },
  props: [
    "article"
  ]
};
</script>

<style lang="scss">
@import "@/assets/css/main/articleList.scss";
</style>




