import axios from '@/axios.js'

var api = {
    getAuthor(id){
        return axios.get(`/api/author?id=${id}`);
    },
    getArticleList(page){
        return axios.get('/api/articleList',{params:page})
    },
    getArticle(parmas){
        return axios.get(`/api/article/:id?id=${parmas}`,)
    },
    writeArticle(parmas){
        return axios.post('/api/article', parmas)
    },
    imgUpload(parmas){
        return axios({
            url: '/api/upload',
            method: 'post',
            data: parmas,
            headers: { 'Content-Type': 'multipart/form-data' },
        })
    },
    login(parmas){
        return axios({
            url: '/api/admin/login',
            method: 'post',
            data: parmas
        })
    },
    upArticleList(parmas){
        return axios({
            url: '/api/articleList',
            method: 'post',
            data: parmas
        })
    },
    upArticle(parmas){
        return axios({
            url: '/api/article',
            method: 'post',
            data: parmas
        })
    }
}

export default api;