<template>
  <div class="article-container">
    <div class="article-title">
    <h1>{{articleHeader.title}}</h1>
    <div class="article-aside">
      <span>发布日期：{{date}}</span>
      <span class="type">分类：{{articleHeader.type}}</span>
    </div>
    <blockquote>
      {{articleHeader.intridyce}}
    </blockquote>
    </div>
    <div class="content" v-html="content"> 
    </div>
  </div>
</template>

<script>
// import api from '@/api'
export default {
  data(){
    return {
      articleHeader: {},
      content: this.$store.state.content
    }
  },
  computed:{
    date(){
      if(this.articleHeader.date){
        return this.articleHeader.date.slice(0,10)
      }
      return null;

    }
  },
  beforeRouteEnter: (to, from, next) => {
    next(vm=>{
      vm.articleHeader = to.query;
    });
  }
}
</script>

<style lang="scss">
    @import '@/assets/css/article/index.scss';
</style>