<template>
    <div class="pager">
        <a @click="changePage(1)" class="pager-item" :class="{disable: value === 1}">首页</a>
        <a @click="changePage(value - 1)" class="pager-item" :class="{disable: value === 1}">上一页</a>
        <a @click="changePage(item)"  class="pager-item" :class="{active: item ===  value}" v-for="(item,index) in numbers" :key="index">{{item}}</a>
        <a  class="pager-item">下一页</a>
        <a  class="pager-item">尾页</a>
    </div>
</template>

<script>
export default {
    props:{
        value:{
            type: Number,
            default: 1,
        },
        pageSize: {
            type: Number,
            default: 10,
        },
        panelNumber: {
            type: Number,
            default: 5
        }
    },
    computed:{
        pageNumber(){
            return Math.ceil(this.total / this.pageSize)
        },
        numbers(){
            var min = this.value - Math.floor(this.panelNumber / 2);
            if(min < 1){
                min = 1;
            }
            //最大页码数字
            var max = min + this.panelNumber - 1;
            if(max > this.pageNumber){
                max = this.pageNumber;
            }
            const arr = [];
            for(let i = min; i <= max; i++){
                arr.push(i);
            }
            return arr;
        },
    },
        methods: {
            changePage(newPage){
                if(newPage < 1){
                    newPage = 1;
                }else if(newPage > this.pageNumber){
                    newPage = this.pageNumber;
                }
                this.$emit('input',newPage);
            }
        },
    }

</script>

<style lang="scss">
    @import '@/assets/css/pager.scss';
</style>